import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from './../../../services/backend/admin.service';
import { FormGroup,  FormBuilder,  Validators, FormControl } from '@angular/forms';
import { Subject } from 'rxjs';
@Component({
	templateUrl: 'edit.component.html'
})
export class EditComponent implements OnInit {
	angForm: FormGroup;
	submitted = false;
	dtOptions: DataTables.Settings = {};
	services: any;
	data:any;
	name:any;
	serviceId: any;
	dtTrigger: Subject<any> = new Subject();
	constructor(private service: AdminService, private router: Router, private route: ActivatedRoute, private fb: FormBuilder) {
		this.LoginForm();
	}
	LoginForm() {
		this.angForm = this.fb.group({
			serviceName: ['', Validators.required]
		});
    }
	
	/* On Submit service Update function is called*/
	
    submitService() {
        this.serviceId = this.route.snapshot.paramMap.get('serviceId');
        if(this.angForm.value.serviceName){
            this.service.updateservicebyid(this.serviceId, this.angForm.value.serviceName).subscribe(res => {
				this.data = res.message;
				setTimeout(() => {
					this.router.navigate(['/admin/services/list'])
				}, 2000);
			}, err => {
				console.log(err); //   this.router.navigate(['services']);
			});
        } else {
            window.alert('Please add the service name');
        }
        
    }

	get f() { return this.angForm.controls; }
	ngOnInit(): void {
		this.dtOptions = {
			pagingType: 'full_numbers',
			pageLength: 3,
			columnDefs: [{ orderable: false, targets: [3] }]
		};
		this.serviceId = this.route.snapshot.paramMap.get('serviceId');
		this.service.getServiceById(this.serviceId).subscribe(res => {
			console.log(res);
			this.services = res.data;
			this.name = this.services.name;
			this.dtTrigger.next();
		}, err => {
			console.log(err); //   this.router.navigate(['login']);
		});
		//http://13.58.114.48:3001/api/admin/editServices/5c544a8515f5bb0c908b3014
	}
}